"use client"

import Link from "next/link"
import { useCart } from "@/contexts/CartContext"

export default function CartPage() {
  const { items, updateQuantity, removeItem, totalItems, totalPrice } = useCart()

  if (items.length === 0) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="py-16 sm:py-24 text-center">
          <h1 className="text-4xl font-bold mb-8">Your Cart</h1>
          <p className="text-gray-600 mb-8">Your cart is empty</p>
          <Link 
            href="/shop" 
            className="inline-block bg-black text-white px-8 py-3 rounded-md hover:bg-gray-800 transition-colors"
          >
            Continue Shopping
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div className="py-16 sm:py-24">
        <h1 className="text-4xl font-bold mb-8">Your Cart</h1>
        
        {/* Cart Items */}
        <div className="divide-y">
          {items.map((item) => (
            <div key={item.id} className="py-8 flex gap-8">
              {/* Product Image */}
              <div className="aspect-square w-24 h-24 flex-shrink-0 overflow-hidden rounded-md">
                <img
                  src={item.image}
                  alt={item.name}
                  className="h-full w-full object-cover object-center"
                />
              </div>

              {/* Product Details */}
              <div className="flex flex-1 flex-col">
                <div className="flex justify-between">
                  <div>
                    <h3 className="text-lg font-medium">
                      <Link href={`/shop/${item.id}`} className="hover:text-gray-600">
                        {item.name}
                      </Link>
                    </h3>
                    <p className="mt-1 text-gray-600">${item.price.toFixed(2)}</p>
                  </div>
                  <button
                    onClick={() => removeItem(item.id)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    Remove
                  </button>
                </div>

                {/* Quantity Controls */}
                <div className="mt-4 flex items-center">
                  <button
                    onClick={() => updateQuantity(item.id, item.quantity - 1)}
                    className="rounded-md border px-2 py-1 text-sm hover:bg-gray-50"
                  >
                    -
                  </button>
                  <span className="mx-4 text-gray-600">{item.quantity}</span>
                  <button
                    onClick={() => updateQuantity(item.id, item.quantity + 1)}
                    className="rounded-md border px-2 py-1 text-sm hover:bg-gray-50"
                  >
                    +
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Cart Summary */}
        <div className="mt-12 border-t pt-8">
          <div className="flex justify-between text-base font-medium">
            <p>Subtotal ({totalItems} items)</p>
            <p>${totalPrice.toFixed(2)}</p>
          </div>
          <p className="mt-1 text-sm text-gray-500">
            Shipping and taxes will be calculated at checkout.
          </p>

          <div className="mt-8 space-y-4">
            <Link href="/checkout">
              <button className="w-full bg-black text-white py-4 px-4 rounded-md hover:bg-gray-800 transition-colors">
                Proceed to Checkout
              </button>
            </Link>
            <div className="flex justify-center">
              <Link 
                href="/shop"
                className="text-sm text-gray-600 hover:text-gray-800"
              >
                Continue Shopping â†’
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
